import React, { useState, useEffect } from "react";
import { CssBaseline } from "@material-ui/core";
import { commerce } from "./lib/commerce";
import Products from "./components/Products/Products";
import Navbar from "./components/Navbar/Navbar";
import Cart from "./components/Cart/Cart";
import Checkout from "./components/CheckoutForm/Checkout/Checkout";
import ProductView from "./components/ProductView/ProductView";
import Manga from "./components/Manga/Manga";
import Footer from "./components/Footer/Footer";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import loadingImg from "./assets/loader.gif";
import "./style.css";
import Fiction from "./components/Fiction/Fiction";
import Biography from "./components/Bio/Biography";

const App = () => {
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [products, setProducts] = useState([]);
  const [mangaProducts, setMangaProducts] = useState([]);
  const [fictionProducts, setFictionProducts] = useState([]);
  const [bioProducts, setBioProducts] = useState([]);
  const [featureProducts, setFeatureProducts] = useState([]);
  const [cart, setCart] = useState({});
  const [order, setOrder] = useState({});
  const [errorMessage, setErrorMessage] = useState("");

  const fetchProducts = async () => {
    try {
      const response = await commerce.products.list();
      if (response && response.data) {
        setProducts(response.data);
      } else {
        console.error('Invalid response from commerce.products.list()');
        setProducts([]);
      }
    } catch (error) {
      console.error('Error fetching products:', error);
      setProducts([]);
    }
  };

  const fetchMangaProducts = async () => {
    try {
      const response = await commerce.products.list({
        category_slug: ["manga"],
      });
      if (response && response.data) {
        setMangaProducts(response.data);
      } else {
        console.error('Invalid response from commerce.products.list() for manga');
        setMangaProducts([]);
      }
    } catch (error) {
      console.error('Error fetching manga products:', error);
      setMangaProducts([]);
    }
  };

  const fetchFeatureProducts = async () => {
    try {
      const response = await commerce.products.list({
        category_slug: ["featured"],
      });
      if (response && response.data) {
        setFeatureProducts(response.data);
      } else {
        console.error('Invalid response from commerce.products.list() for featured');
        setFeatureProducts([]);
      }
    } catch (error) {
      console.error('Error fetching featured products:', error);
      setFeatureProducts([]);
    }
  };

  const fetchFictionProducts = async () => {
    try {
      const response = await commerce.products.list({
        category_slug: ["fiction"],
      });
      if (response && response.data) {
        setFictionProducts(response.data);
      } else {
        console.error('Invalid response from commerce.products.list() for fiction');
        setFictionProducts([]);
      }
    } catch (error) {
      console.error('Error fetching fiction products:', error);
      setFictionProducts([]);
    }
  };

  const fetchBioProducts = async () => {
    try {
      const response = await commerce.products.list({
        category_slug: ["biography"],
      });
      if (response && response.data) {
        setBioProducts(response.data);
      } else {
        console.error('Invalid response from commerce.products.list() for biography');
        setBioProducts([]);
      }
    } catch (error) {
      console.error('Error fetching biography products:', error);
      setBioProducts([]);
    }
  };

  const fetchCart = async () => {
    try {
      const cart = await commerce.cart.retrieve();
      if (cart) {
        setCart(cart);
      } else {
        console.error('Invalid response from commerce.cart.retrieve()');
        setCart({});
      }
    } catch (error) {
      console.error('Error fetching cart:', error);
      setCart({});
    }
  };

  const handleAddToCart = async (productId, quantity) => {
    try {
      const item = await commerce.cart.add(productId, quantity);
      if (item && item.cart) {
        setCart(item.cart);
      } else {
        console.error('Invalid response from commerce.cart.add()');
      }
    } catch (error) {
      console.error('Error adding item to cart:', error);
    }
  };

  const handleUpdateCartQty = async (lineItemId, quantity) => {
    try {
      const response = await commerce.cart.update(lineItemId, { quantity });
      if (response && response.cart) {
        setCart(response.cart);
      } else {
        console.error('Invalid response from commerce.cart.update()');
      }
    } catch (error) {
      console.error('Error updating cart quantity:', error);
    }
  };

  const handleRemoveFromCart = async (lineItemId) => {
    try {
      const response = await commerce.cart.remove(lineItemId);
      if (response && response.cart) {
        setCart(response.cart);
      } else {
        console.error('Invalid response from commerce.cart.remove()');
      }
    } catch (error) {
      console.error('Error removing item from cart:', error);
    }
  };

  const handleEmptyCart = async () => {
    try {
      const response = await commerce.cart.empty();
      if (response && response.cart) {
        setCart(response.cart);
      } else {
        console.error('Invalid response from commerce.cart.empty()');
      }
    } catch (error) {
      console.error('Error emptying cart:', error);
    }
  };

  const refreshCart = async () => {
    try {
      const newCart = await commerce.cart.refresh();
      if (newCart) {
        setCart(newCart);
      } else {
        console.error('Invalid response from commerce.cart.refresh()');
      }
    } catch (error) {
      console.error('Error refreshing cart:', error);
    }
  };

  const handleCaptureCheckout = async (checkoutTokenId, newOrder) => {
    try {
      const incomingOrder = await commerce.checkout.capture(
        checkoutTokenId,
        newOrder
      );

      setOrder(incomingOrder);

      refreshCart();
    } catch (error) {
      setErrorMessage(error.data.error.message);
    }
  };

  useEffect(() => {
    fetchProducts();
    fetchFeatureProducts();
    fetchCart();
    fetchMangaProducts();
    fetchFictionProducts();
    fetchBioProducts();
  }, []);

  const handleDrawerToggle = () => setMobileOpen(!mobileOpen);

  return (
    <div>
      {products.length > 0 ? (
        <>
          <Router>
            <div style={{ display: "flex" }}>
              <CssBaseline />
              <Navbar
                totalItems={cart.total_items}
                handleDrawerToggle={handleDrawerToggle}
              />
              <Switch>
                <Route exact path="/">
                  <Products
                    products={products}
                    featureProducts={featureProducts}
                    onAddToCart={handleAddToCart}
                    handleUpdateCartQty
                  />
                </Route>
                <Route exact path="/cart">
                  <Cart
                    cart={cart}
                    onUpdateCartQty={handleUpdateCartQty}
                    onRemoveFromCart={handleRemoveFromCart}
                    onEmptyCart={handleEmptyCart}
                  />
                </Route>
                <Route path="/checkout" exact>
                  <Checkout
                    cart={cart}
                    order={order}
                    onCaptureCheckout={handleCaptureCheckout}
                    error={errorMessage}
                  />
                </Route>
                <Route path="/product-view/:id" exact>
                  <ProductView />
                </Route>
                <Route path="/manga" exact>
                  <Manga
                    mangaProducts={mangaProducts}
                    onAddToCart={handleAddToCart}
                    handleUpdateCartQty
                  />
                </Route>
                <Route path="/fiction" exact>
                  <Fiction
                    fictionProducts={fictionProducts}
                    onAddToCart={handleAddToCart}
                    handleUpdateCartQty
                  />
                </Route>
                <Route path="/biography" exact>
                  <Biography
                    bioProducts={bioProducts}
                    onAddToCart={handleAddToCart}
                    handleUpdateCartQty
                  />
                </Route>
              </Switch>
            </div>
          </Router>
          <Footer />
        </>
      ) : (
        <div className="loader">
          <img src={loadingImg} alt="Loading" />
        </div>
      )}
    </div>
  );
};

export default App;
